(function () {
    function removeLoading() {
        window.setTimeout(function () {
            const body = document.body;
            body.classList.add('loaded');
            body.classList.remove("loading");
        }, 300);
    }

    function pageOneParallaxEffect() {
        window.addEventListener('scroll', function (e) {
            let scrolled = window.pageYOffset;
            const page1 = document.querySelector('#page-1');
            page1.style.backgroundPositionY = `${scrolled * 0.7}px`;
        });
    }

    function scrollTopAnimation() {
        const scrollDuration = 450;

        function scrollToTop() {
            const scrollHeight = window.scrollY,
                scrollStep = Math.PI / (scrollDuration / 15),
                cosParameter = scrollHeight / 2;
            var scrollCount = 0,
                scrollMargin,
                scrollInterval = setInterval(function () {
                    if (window.scrollY != 0) {
                        scrollCount = scrollCount + 1;
                        scrollMargin = cosParameter - cosParameter * Math.cos(scrollCount * scrollStep);
                        window.scrollTo(0, (scrollHeight - scrollMargin));
                    }
                    else clearInterval(scrollInterval);
                }, 15);
            return true;
        }

        document.getElementById("slide-top").onclick = scrollToTop
    }

    function replaceImgSrc() {
        const imgDefer = Array.from(document.getElementsByTagName('img'));
        const replacementObj = Array.from(document.querySelectorAll('noscript')).reduce((acc, next) => {
            const src = next.textContent.match(/\ssrc="([^"]*)"/i);
            const dataSrc = next.textContent.match(/\sdata-src="([^"]*)"/i);
            if (dataSrc && dataSrc[1] && src && src[1]) {
                acc[dataSrc[1]] = src[1]
            }
            return acc;
        }, {});
        imgDefer.forEach(img => {
            let dataSrc = img.getAttribute('data-src');
            if (dataSrc && replacementObj[dataSrc]) {
                img.setAttribute('src', replacementObj[dataSrc]);
            }
        })
    }

    function init() {
        removeLoading();
        pageOneParallaxEffect();
        scrollTopAnimation();
        replaceImgSrc();
    }

    window.onload = init;
})();
